.trans-curtain {
  position: absolute;
  left: -25%;
  top: 0;
  height: 100%;
  width: 150%;
  z-index: 11;
  background-color: #bcffd0;
  -webkit-transform: skewX(-10deg);
          transform: skewX(-10deg);
  -webkit-animation-name: trans;
          animation-name: trans;
  -webkit-animation-duration: 1.2s;
          animation-duration: 1.2s;
}
@-webkit-keyframes trans {
  0%   {left: -175%;}
  20%   {left: -175%;}
  100%   {left: -25%;}
}
@keyframes trans {
  0%   {left: -175%;}
  20%   {left: -175%;}
  100%   {left: -25%;}
}

.trans-curtain-hidden {
  left: -175%;
  -webkit-animation-name: trans-hidden;
          animation-name: trans-hidden;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
}
@-webkit-keyframes trans-hidden {
  0%   {left: -25%;}
  20%   {left: -25%;}
  100%   {left: -175%;}
}
@keyframes trans-hidden {
  0%   {left: -25%;}
  20%   {left: -25%;}
  100%   {left: -175%;}
}
.trans-curtain > img {
  position: absolute;

  top: 50%;
  left: 50%;
  margin-left: 0;
  height: 28vh;
  -webkit-transform: translate(-50%, -50%) skewX(10deg);
          transform: translate(-50%, -50%) skewX(10deg);
  z-index: 4;
}

.main-trans-curtain {
  position: absolute;
  left: -25%;
  top: 0;
  height: 100%;
  width: 150%;
  z-index: 11;
  background-color: #bcffd0;
  -webkit-transform: skewX(-10deg);
          transform: skewX(-10deg);
  -webkit-animation-name: trans;
          animation-name: trans;
  -webkit-animation-duration: 1.2s;
          animation-duration: 1.2s;
}
.main-trans-curtain-hidden {
  left: -175%;
  -webkit-animation-name: trans-hidden;
          animation-name: trans-hidden;
  -webkit-animation-duration: 0.1s;
          animation-duration: 0.1s;
}
